.btn {
  padding: vw(20.5) vw(59);
  background: var(--color-main);
  border-radius: vw(20);

  font-weight: 700;
  font-size: vw(16);
  line-height: vw(19);
  color: var(--color-white);

  @include medium {
    padding: lg-vw(20.5) lg-vw(55.5);
    border-radius: lg-vw(20);
    font-size: lg-vw(16);
    line-height: lg-vw(19);
  }

  @include small {
    padding: md-vw(20.5) md-vw(48);
    border-radius: md-vw(20);
    font-size: md-vw(16);
    line-height: md-vw(19);
  }

  @include mobile {
    padding: sm-vw(20.5) sm-vw(55);
    border-radius: sm-vw(20);
    font-size: sm-vw(16);
    line-height: sm-vw(19);
  }
}

.heading {
  font-weight: 700;
  font-size: vw(40);
  line-height: 100%;

  @include medium {
    font-size: lg-vw(40);
  }

  @include small {
    font-size: md-vw(40);
  }

  @include mobile {
    font-size: sm-vw(20);
    line-height: 130%;
  }
}

.desc {
  font-size: vw(16);
  line-height: 150%;

  @include medium {
    font-size: lg-vw(16);
  }

  @include small {
    font-size: md-vw(16);
  }

  @include mobile {
    font-size: sm-vw(16);
  }
}