// CONTAINER
.container {
  max-width: vw(1130); //1100px
  padding: 0 vw(15);
  margin: 0 auto;

  @include medium {
    max-width: lg-vw(1130);
    padding: 0 lg-vw(15);
  }

  @include small {
    max-width: md-vw(1130);
    padding: 0 md-vw(15);
  }

  @include mobile {
    max-width: sm-vw(1130);
    padding: 0 sm-vw(15);
  }
}