.header {
  height: vw(80);
  background: var(--color-main);

  @include medium {
    height: lg-vw(80);
  }

  @include small {
    height: md-vw(133);
  }

  @include mobile {
    height: md-vw(80);
  }
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;

  height: 100%;

  @include small {
    flex-direction: column;
    justify-content: center;
  }

  @include mobile {
    flex-direction: row;
    justify-content: space-between;
  }
}

.header__logo {
  display: inline-block;

  svg {
    width: vw(94);
    height: vw(21);

    @include medium {
      width: lg-vw(94);
      height: lg-vw(21);
    }

    @include small {
      width: md-vw(94);
      height: md-vw(21);
    }

    @include mobile {
      width: sm-vw(72);
      height: sm-vw(16);
    }
  }

  @include small {
    margin-bottom: md-vw(30);
  }

  @include mobile {
    margin-bottom: 0;
  }
}

.nav {
  @include mobile {
    display: none;
  }
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__item {
  a {
    font-size: vw(18);
    line-height: vw(21);
    color: var(--color-text-header);
    padding: vw(14);
    border-radius: vw(20);

    @include medium {
      font-size: lg-vw(18);
      line-height: lg-vw(21);
      padding: lg-vw(14);
      border-radius: lg-vw(20);
    }

    @include small {
      font-size: md-vw(18);
      line-height: md-vw(21);
      padding: md-vw(14);
      border-radius: md-vw(20);
    }
  }

  &:not(:last-child) {
    margin-right: vw(28);
    
    @include medium {
      margin-right: lg-vw(28);
    }

    @include small {
      margin-right: md-vw(28);
    }
  }
}

.header__burger {
  display: none;

  @include mobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    width: sm-vw(30);
    height: sm-vw(30);
    padding: sm-vw(6.55) sm-vw(4.7);

    span {
      width: 100%;
      height: sm-vw(2);

      border-radius: sm-vw(20);
      background: var(--color-white);
    }
  }
}