.hero {
  background: radial-gradient(44.07% 44.07% at 67.78% 55.93%, #555681 0%, #212133 100%);
}

.hero__container {
  position: relative;
}

.hero__heading {
  max-width: vw(459);

  font-weight: 700;
  font-size: vw(60);
  line-height: 100%;
  color: var(--color-text-hero);

  padding-top: vw(130);
  margin-top: 0;
  margin-bottom: vw(30);

  @include medium {
    max-width: lg-vw(459);
    font-size: lg-vw(50);
    padding-top: lg-vw(130);
    margin-bottom: lg-vw(30);
  }

  @include small {
    max-width: md-vw(385);
    font-size: md-vw(50);
    padding-top: md-vw(77);
    margin-bottom: md-vw(30);
  }

  @include mobile {
    max-width: sm-vw(226);
    font-size: sm-vw(30);
    padding-top: sm-vw(50);
    margin-bottom: sm-vw(15);
  }
}

.hero__desc {
  max-width: vw(444);

  font-size: vw(18);
  line-height: 150%;
  color: var(--color-text-hero);

  margin-bottom: vw(70);

  @include medium {
    max-width: lg-vw(345);
    font-size: lg-vw(18);

    margin-bottom: lg-vw(70);
  }

  @include small {
    max-width: md-vw(345);
    font-size: md-vw(18);

    margin-bottom: md-vw(70);
  }

  @include mobile {
    max-width: sm-vw(290);
    font-size: sm-vw(16);

    margin-bottom: sm-vw(50);
  }
}

.hero__btn {
  padding: vw(24) vw(75.5);
  font-size: vw(18);
  line-height: vw(21);

  margin-bottom: vw(130);

  @include medium {
    padding: lg-vw(24) lg-vw(75.5);
    font-size: lg-vw(18);
    line-height: lg-vw(21);

    margin-bottom: lg-vw(130);
  }

  @include small {
    padding: md-vw(24) md-vw(75.5);
    font-size: md-vw(18);
    line-height: md-vw(21);

    margin-bottom: md-vw(130);
  }

  @include mobile {
    padding: sm-vw(24) sm-vw(53.5);
    font-size: sm-vw(16);
    line-height: sm-vw(19);

    margin-bottom: sm-vw(50);
  }
}


.hero__bbg {
  position: absolute;
  right: 0;
  top: vw(15);

  width: vw(540);
  height: vw(593);

  @include medium {
    top: lg-vw(125);
    right: lg-vw(15);

    width: lg-vw(311.14);
    height: lg-vw(342.26);
  }

  @include small {
    display: none;
  }
}

.hero__sbg {
  position: absolute;
  right: vw(413);
  top: vw(64);

  width: vw(203.66);
  height: vw(152.75);

  transform: rotate(-13.93deg);

  @include medium {
    right: lg-vw(194.4);
    top: lg-vw(117);
  }

  @include small {
    display: none;
  }
}