.preference {
  padding: vw(80) 0;

  @include medium {
    padding: lg-vw(80) 0;
  }

  @include small {
    padding: md-vw(80) 0;
  }

  @include mobile {
    padding: sm-vw(50) 0;
  }
}

.preference__container {
  max-width: vw(1160);

  @include medium {
    max-width: lg-vw(1160);
  }

  @include small {
    max-width: md-vw(1160);
  }

  @include mobile {
    max-width: sm-vw(1160);
  }
}

.preference__titleBox {
  padding-left: vw(190);
  margin-left: vw(15);

  background: url('../img/preference/bg.png') no-repeat;
  background-size: vw(160) vw(142);
  background-position: center left;

  @include medium {
    padding-left: lg-vw(241);
    margin-left: lg-vw(15);

    background-size: lg-vw(211) lg-vw(187);
  }

  @include small {
    padding-left: md-vw(180);
    margin-left: md-vw(15);

    background-size: md-vw(150) md-vw(134);
  }

  @include mobile {
    padding-left: 0;
    margin-left: sm-vw(15);

    background: none;
  }
}

.preference__heading {
  max-width: vw(380);

  color: var(--color-heading-preference);
  margin-bottom: vw(30);

  @include medium {
    max-width: lg-vw(380);

    margin-bottom: lg-vw(30);
  }

  @include small {
    max-width: md-vw(330);

    margin-bottom: md-vw(30);
  }

  @include mobile {
    max-width: sm-vw(290);

    margin-bottom: sm-vw(15);
  }
}

.preference__desc {
  max-width: vw(585);

  color: var(--color-heading-preference);
  margin-bottom: vw(30);

  @include medium {
    max-width: lg-vw(450);

    margin-bottom: lg-vw(30);
  }

  @include small {
    max-width: md-vw(330);

    margin-bottom: md-vw(30);
  }

  @include mobile {
    max-width: sm-vw(290);
  }
}


.row {
  --bs-gutter-x: vw(30);

  @include medium {
    --bs-gutter-x: lg-vw(30);
  }

  @include small {
    --bs-gutter-x: md-vw(30);
  }

  @include mobile {
    --bs-gutter-y: sm-vw(30);
  }
}

.col-lg-4,
.col-sm-4,
.col-12 {
  padding: 0 vw(15);

  @include medium {
    padding: 0 lg-vw(15);
  }

  @include small {
    padding: 0 md-vw(15);
  }

  @include mobile {
    padding: sm-vw(15) 0;
  }
}

.preference__item {
  display: flex;
  align-items: center;
  justify-content: flex-start;

  svg {
    display: inline-block;

    width: vw(80);
    min-width: vw(80);
    max-width: vw(80);
    height: vw(80);
    min-height: vw(80);
    max-height: vw(80);

    margin-right: vw(30);

    @include medium {
      width: lg-vw(80);
      min-width: lg-vw(80);
      max-width: lg-vw(80);
      height: lg-vw(80);
      min-height: lg-vw(80);
      max-height: lg-vw(80);

      margin-bottom: lg-vw(15);
    }

    @include small {
      width: md-vw(80);
      min-width: md-vw(80);
      max-width: md-vw(80);
      height: md-vw(80);
      min-height: md-vw(80);
      max-height: md-vw(80);

      margin-bottom: md-vw(15);
    }

    @include mobile {
      width: sm-vw(80);
      min-width: sm-vw(80);
      max-width: sm-vw(80);
      height: sm-vw(80);
      min-height: sm-vw(80);
      max-height: sm-vw(80);

      margin-right: sm-vw(30);
    }
  }

  @include medium {
    flex-wrap: wrap;
  }

  @include mobile {
    flex-wrap: nowrap;
  }
}

.preference__text {
  font-weight: 700;
  font-size: vw(16);
  line-height: 120%;
  color: var(--color-desc-preference);

  @include medium {
    font-size: lg-vw(16);
  }

  @include small {
    font-size: md-vw(16);
  }

  @include mobile {
    font-size: sm-vw(16);
  }
}