// SCROLLBAR
* {
  scrollbar-width: thin;
  scrollbar-color: var(--color-main) var(--color-bg-teachers);
}

*::-webkit-scrollbar {
  height: 12px;
  width: 12px;
}

*::-webkit-scrollbar-track {
  background: var(--color-bg-teachers);
}

*::-webkit-scrollbar-thumb {
  background-color: var(--color-main);
  border-radius: 5px;
  border: 3px solid var(--color-bg-teachers);
}


// BTN
.btn {
  transition: background .3s;
}

.btn:hover,
.btn:focus,
.btn:active {
  outline: none;
  background: rgba(#3F2AFF, .6);
}


// MENU
.nav__item,
.burger__item {
  a {
    transition: background .3s;
  }
}

.nav__item,
.burger__item {
  a:hover,
  a:focus,
  a:active {
    outline: none;
    background: rgba(#FFF, .07);
  }
}


// SOC
.footer__item {
  svg {
    transition: fill .3s;
  }
}

.footer__item {
  svg:hover,
  svg:focus,
  svg:active {
    outline: none;
    fill: var(--color-main);
  }
}