.learn {
  padding: vw(80) 0;

  @include medium {
    padding: lg-vw(80) 0;
  }

  @include small {
    padding: md-vw(80) 0;
  }

  @include mobile {
    padding: sm-vw(50) 0;
  }
}

.learn__heading {
  color: var(--color-heading-learn);

  margin-bottom: vw(30);
  margin-left: vw(15);

  @include medium {
    margin-bottom: lg-vw(30);
    margin-left: lg-vw(15);
  }

  @include small {
    margin-bottom: md-vw(30);
    margin-left: md-vw(15);
  }

  @include mobile {
    margin-bottom: sm-vw(30);
    margin-left: sm-vw(15);
  }
}

.learn__container {
  max-width: vw(1160);

  @include medium {
    max-width: lg-vw(1160);
  }

  @include small {
    max-width: md-vw(1160);
  }

  @include mobile {
    max-width: sm-vw(1160);
  }
}

.col-12,
.col-sm-6 {
  padding: vw(15);

  @include medium {
    padding: lg-vw(15);
  }

  @include small {
    padding: md-vw(15);
  }

  @include mobile {
    padding: sm-vw(15);
  }
}

.learn__item {
  height: 100%;
}

.learn__box {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  height: 100%;
  min-height: vw(480);

  background: var(--color-bg-learn);
  border-radius: vw(20);

  @include medium {
    min-height: lg-vw(556);
    border-radius: lg-vw(20);
  }

  @include small {
    min-height: md-vw(653);
    border-radius: md-vw(20);
  }

  @include mobile {
    min-height: sm-vw(653);
    border-radius: sm-vw(20);
  }
}

.learn__img {
  width: 100%;
  object-fit: cover;
  border-radius: vw(20) vw(20) 0 0;

  margin-bottom: vw(30);

  @include medium {
    margin-bottom: lg-vw(30);
    border-radius: lg-vw(20) lg-vw(20) 0 0;
  }

  @include small {
    height: md-vw(180);
    margin-bottom: md-vw(30);
    border-radius: md-vw(20) md-vw(20) 0 0;
  }

  @include mobile {
    height: sm-vw(180);
    margin-bottom: sm-vw(30);
    border-radius: sm-vw(20) sm-vw(20) 0 0;
  }
}

.learn__low-heading {
  font-weight: 700;
  font-size: vw(18);
  line-height: 150%;
  color: var(--color-heading-learn);

  margin-bottom: vw(15);
  padding: 0 vw(30);

  @include medium {
    font-size: lg-vw(18);
    margin-bottom: lg-vw(15);
    padding: 0 lg-vw(30);
  }

  @include small {
    font-size: md-vw(18);
    margin-bottom: md-vw(15);
    padding: 0 md-vw(30);
  }

  @include mobile {
    font-size: sm-vw(18);
    margin-bottom: sm-vw(15);
    padding: 0 sm-vw(30);
  }
}

.learn__low-desc {
  font-size: vw(16);
  line-height: 150%;
  color: var(--color-desc-learn);

  margin-bottom: vw(32);
  padding: 0 vw(30);

  @include medium {
    font-size: lg-vw(16);
    margin-bottom: lg-vw(32);
    padding: 0 lg-vw(30);
  }

  @include small {
    font-size: md-vw(16);
    margin-bottom: md-vw(32);
    padding: 0 md-vw(30);
  }

  @include mobile {
    font-size: sm-vw(16);
    margin-bottom: sm-vw(32);
    padding: 0 sm-vw(30);
  }
}

.learn__btn {
  position: absolute;
  left: vw(30);
  bottom: vw(30);
  text-align: center;
  
  @include medium {
    left: lg-vw(30);
    bottom: lg-vw(30);
  }

  @include small {
    left: md-vw(30);
    bottom: md-vw(30);
    right: md-vw(30);
  }

  @include mobile {
    left: sm-vw(30);
    bottom: sm-vw(30);
    right: sm-vw(30);
  }
}

.learn__time {
  position: absolute;
  top: vw(10);
  right: vw(10);

  display: flex;
  align-items: center;

  svg {
    width: vw(24);
    height: vw(24);

    margin-right: vw(5);

    @include medium {
      width: lg-vw(24);
      height: lg-vw(24);

      margin-right: lg-vw(5);
    }

    @include small {
      width: md-vw(24);
      height: md-vw(24);

      margin-right: md-vw(5);
    }

    @include mobile {
      width: sm-vw(24);
      height: sm-vw(24);

      margin-right: sm-vw(5);
    }
  }

  span {
    font-size: vw(16);
    line-height: vw(19);
    color: var(--color-white);

    @include medium {
      font-size: lg-vw(16);
      line-height: lg-vw(19);
    }

    @include small {
      font-size: md-vw(16);
      line-height: md-vw(19);
    }

    @include mobile {
      font-size: sm-vw(16);
      line-height: sm-vw(19);
    }
  }

  @include medium {
    top: lg-vw(10);
    right: lg-vw(10);
  }

  @include small {
    top: md-vw(10);
    right: md-vw(10);
  }

  @include mobile {
    top: sm-vw(10);
    right: sm-vw(10);
  }
}