.burger {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  min-width: 60%;

  background: var(--color-main);
  z-index: 10000;

  transform: translateX(101%);
  opacity: 0;
  visibility: hidden;
  transition-property: opacity, visibility, transform;
  transition-duration: .4s;
  transition-timing-function:  ease-in-out;

  &.is_active {
    transform: none;
    opacity: 1;
    visibility: visible;
  }

  &__close {
    position: relative;
    display: inline-block;
    right: sm-vw(-156);
    top: sm-vw(7);

    width: sm-vw(30);
    height: sm-vw(30);
    padding: sm-vw(5);

    &::before,
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;

      width: 100%;
      height: sm-vw(2);
      background: var(--color-white);

      transform: translate(-50%, -50%) rotate(45deg);
    }

    &::before {
      transform: translate(-50%, -50%) rotate(135deg);
    }
  }
}

.burger__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  padding: sm-vw(28) sm-vw(30) sm-vw(20) sm-vw(20);
}

.burger__item {
  a {
    font-size: sm-vw(18);
    line-height: sm-vw(21);
    color: var(--color-text-header);

    padding: sm-vw(10);
    border-radius: sm-vw(20);
  }

  &:not(:last-child) {
    margin-bottom: sm-vw(30);
  }
}