.footer {
  height: vw(95);

  background: var(--color-bg-footer);

  @include medium {
    height: lg-vw(95);
  }

  @include small {
    height: md-vw(95);
  }

  @include mobile {
    height: sm-vw(95);
  }
}

.footer__container {
  display: flex;
  flex-direction: column;
  justify-content: center;

  height: 100%;
}

.footer__list {
  display: flex;
  align-items: center;
  justify-content: center;

  height: 100%;
}

.footer__item {
  svg {
    width: vw(24);
    height: vw(24);

    fill: var(--color-svg-footer);

    @include medium {
      width: lg-vw(24);
      height: lg-vw(24);
    }

    @include small {
      width: md-vw(24);
      height: md-vw(24);
    }

    @include mobile {
      width: sm-vw(24);
      height: sm-vw(24);
    }
  }

  &:not(:last-child) {
    margin-right: vw(30);

    @include medium {
      margin-right: lg-vw(30);
    }

    @include small {
      margin-right: md-vw(30);
    }

    @include mobile {
      margin-right: sm-vw(30);
    }
  }
}

.footer__skillbox {
  display: flex;
  align-items: flex-end;
  justify-content: center;

  width: 100%;
  height: vw(15);

  background: var(--color-svg-footer);

  span {
    font-size: vw(13);
    line-height: vw(15);
    text-transform: uppercase;
    color: var(--color-bg-footer);
    
    strong {
      color: var(--color-main);
    }

    @include medium {
      font-size: lg-vw(13);
      line-height: lg-vw(15);
    }

    @include small {
      font-size: md-vw(13);
      line-height: md-vw(15);
    }

    @include mobile {
      font-size: sm-vw(13);
      line-height: sm-vw(15);
    }
  }

  @include medium {
    height: lg-vw(15);
  }

  @include small {
    height: md-vw(15);
  }

  @include mobile {
    height: sm-vw(15);
  }
}