.teachers {
  padding: vw(80) 0;

  background: var(--color-bg-teachers);

  @include medium {
    padding: lg-vw(80) 0;
  }

  @include small {
    padding: md-vw(80) 0;
  }

  @include mobile {
    padding: sm-vw(50) 0;
  }
}

.teachers__container {
  max-width: vw(1160);

  @include medium {
    max-width: lg-vw(1160);
  }

  @include small {
    max-width: md-vw(1160);
  }

  @include mobile {
    max-width: sm-vw(1160);
  }
}

.teachers__heading {
  color: var(--color-heading-teachers);
  margin-bottom: vw(30);
  margin-left: vw(15);

  @include medium {
    margin-bottom: lg-vw(30);
    margin-left: lg-vw(15);
  }

  @include small {
    margin-bottom: md-vw(30);
    margin-left: md-vw(15);
  }

  @include mobile {
    margin-bottom: sm-vw(30);
    margin-left: sm-vw(15);
  }
}


.teachers__item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.teachers__img {
  width: 100%;

  border-radius: vw(20) vw(20) 0 0;
  margin-bottom: vw(30);

  @include medium {
    border-radius: lg-vw(20) lg-vw(20) 0 0;
    margin-bottom: lg-vw(30);
  }

  @include small {
    border-radius: md-vw(20) md-vw(20) 0 0;
    margin-bottom: md-vw(30);
  }

  @include mobile {
    border-radius: sm-vw(20) sm-vw(20) 0 0;
    margin-bottom: sm-vw(30);
  }
}

.col-lg-3,
.col-sm-6,
.col-6 {
  padding: 0 vw(15);

  @include medium {
    padding: 0 lg-vw(15);
  }

  @include small {
    padding: 0 md-vw(15);
  }

  @include mobile {
    padding: 0 sm-vw(15);
  }
}

.teachers__low-heading {
  font-weight: 700;
  font-size: vw(18);
  line-height: 150%;
  color: var(--color-heading-teachers);

  margin-bottom: vw(15);

  @include medium {
    font-size: lg-vw(18);
    margin-bottom: lg-vw(15);
  }

  @include small {
    font-size: md-vw(18);
    margin-bottom: md-vw(15);
  }

  @include mobile {
    font-size: sm-vw(18);
    margin-bottom: sm-vw(15);
  }
}

.teachers__low-desc {
  font-size: vw(16);
  line-height: 150%;
  color: var(--color-desc-teachers);

  @include medium {
    font-size: lg-vw(16);
  }

  @include small {
    font-size: md-vw(16);
  }

  @include mobile {
    font-size: sm-vw(16);
  }
}